import React from 'react';

const CodeSnippet = ({ children }) => {
  return (
    <div className="max-w-lg justify-center w-full bg-gray-300 rounded-lg m-auto my-4">
      <pre className="w-full">
        <code className="flex flex-wrap bg-gray-300 rounded-lg m-auto">
          { children }
        </code>
      </pre>
    </div>
  );
}

export default CodeSnippet;